@import "vars-custom";

.breadcrumb-custom {
  font-weight: 500 !important;
  color: $breadcrumb-color-custom !important;
  line-height: 0.75rem;
}

.breadcrumb-link-custom {
  position: relative !important;
  color: $breadcrumb-color-custom !important;
}

.breadcrumb-link-custom::after {
  content: "";
  display: block;
  position: absolute;
  width: 0.44rem;
  height: 0.44rem;
  right: -0.63rem;
  top: 0.31rem;
  border-top: 1.7px solid $breadcrumb-color-custom;
  border-right: 1.7px solid $breadcrumb-color-custom;
  transform: rotate(45deg);
}